	module.exports = {"data":{"site":{"siteMetadata":{"title":"Emre Hayirci","author":"Emre Hayirci"}},"markdownRemark":{"id":"C:/Users/emre_/Desktop/emrehayirci/src/works/Game Of Destruction/index.md absPath of file >>> MarkdownRemark","html":"<p>In the GGJ-2017 theme was announced as \"Waves\". So we (Awesome team ,that just met there, of 4 people <em>Ahmet Gönüllüoğlu, Merve Eken, Uğur Kubat and Me</em>) wanted to make a game about seismic waves. Enjoy!</p>\n<p><a href=\"https://globalgamejam.org/2017/games/game-destruction-god\">To get more details and Download the game Game</a></p>\n<p><strong>Built using Unity</strong>.</p>\n<p>A small video:</p>\n<iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/xqitUuxnruk\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>\n<p>Dont let any building Escape! Choose where you create a sismic wave. Much deeper te wave starts it hurts building less but effects wide area, much shalloTaller the building, harder to demolish it. Shallower the wave starts it hurts building more but in narrow area. Dont forget you have limitted energy! If demolish one or more building consecutively 15 times you will be rewarded with a combo bonus!</p>\n<p>Special thanks to organizers of GGJ2017 ATOM for awesomeness </p>","frontmatter":{"title":"Game of Destruction (GGJ 2017)","date":"January 22, 2017","tags":"general"}}},"pathContext":{"path":"/game-of-destruction/"}}

